 esta temática, lo invitamos a ver el siguiente video.")]),_vm._m(4),_vm._m(5),_vm._m(6),_vm._m(7),_c('p',{staticClass:"mb-4"},[_vm._v("Uno de los aspectos más visibles del consumo de sustancias psicoactivas, es su impacto económico. En términos de atención médica, los costos son abrumadores. Las estadísticas muestran que los gastos en tratamiento de adicciones, rehabilitación y servicios de salud mental, han aumentado de manera alarmante en las últimas décadas. Los hospitales y clínicas se ven abrumados por la demanda de atención para problemas relacionados con el abuso de sustancias, lo que a su vez lleva a un aumento en los precios de los servicios de salud. Esto no solo afecta a los individuos que luchan contra la adicción, sino también a las familias que deben asumir la carga financiera de la atención médica.")]),_vm._m(8),_vm._m(9),_vm._m(10),_vm._m(11),_c('p',{staticClass:"mb-4"},[_vm._v("El impacto del consumo de sustancias psicoactivas no se limita a los individuos que las consumen, sino que tiene repercusiones, a largo plazo, para la sociedad en su conjunto. La salud pública se ve comprometida cuando un segmento significativo de la población enfrenta problemas de adicción.")]),_vm._m(12),_c('div',{staticClass:"bg-full-width border-top-9-azul"},[_c('div',{staticClass:"px-5 pt-5 pb-0"},[_c('h2',[_vm._v("Material complementario")]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-8"},[_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3 mb-auto mt-1",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/book.svg")}}),_c('a',{staticClass:"text-small text-primary",attrs:{"target":"_blank","href":"https://elibro.net/es/lc/tecnologicodeloriente/titulos/77226RIDE/VS/PP/Abece-salud-mental-psicoactivas-octubre-2016-minsalud.pdf"}},[_vm._v("Recalde, M. M. & Recalde E. H. (2016). Educación y prevención del consumo problemático de drogas II: sustancias ilegales.")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{