 serie de consecuencias que trascienden el ámbito individual y se extienden a las comunidades y las naciones. La problemática de las drogas no se limita únicamente al uso y abuso de sustancias, sino que también incluye su comercialización, los esfuerzos de regulación y control, así como las respuestas sociales y políticas que se implementan para abordar este desafío. En este contexto, se profundizará en el impacto económico y social que tiene el consumo de sustancias psicoactivas, así como en los estigmas asociados y las consecuencias a largo plazo, para los individuos y la sociedad en su conjunto.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_4","data-aos":"flip-up"}},[_c('div',{staticClass:"d-flex"},[_c('span',{staticClass:"me-3"},[_vm._v("1.4")]),_c('h2',[_vm._v("Costos económicos del consumo de sustancias psicoactivas")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-xl-8"},[_c('div',{staticClass:"row bg-rojo p-4 pb-xl-0 mb-4"},[_c('div',{staticClass:"col-3 d-none d-xl-block"},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-15.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-9 d-flex"},[_c('p',{staticClass:"my-auto pb-xl-4"},[_vm._v("la relación entre el consumo de drogas y el crimen, es innegable. El tráfico de drogas, así como los delitos asociados al consumo, generan una carga adicional para el sistema judicial y de seguridad pública. ")])])]),_c('span',[_vm._v("La policía, los tribunales y las cárceles, se ven inundados por casos relacionados con el narcotráfico y la violencia asociada. Esto implica un uso intensivo de recursos que podrían destinarse a otros sectores importantes, como la educación o la infraestructura. La necesidad de combatir el crimen relacionado con las drogas, también lleva a ga