ón de oportunidades de empleo, la restricción del acceso a la educación y la falta de atención médica adecuada. Este ciclo de exclusión puede ser difícil de romper, puesto que las oportunidades limitadas perpetúan la pobreza, lo que a su vez puede aumentar la vulnerabilidad al consumo de sustancias.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_6","data-aos":"flip-up"}},[_c('div',{staticClass:"d-flex"},[_c('span',{staticClass:"me-3"},[_vm._v("1.6")]),_c('h2',[_vm._v("Consecuencias a largo plazo, para la sociedad")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-4 d-none d-xl-block"},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-18.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-8"},[_c('p',{staticClass:"mb-4"},[_vm._v("Esto se traduce en una disminución general en la calidad de vida, así como en la productividad laboral. La incapacidad de los individuos para participar plenamente en la fuerza laboral, puede afectar significativamente la economía de una región.")]),_c('div',{staticClass:"d-flex align-items-start bg-amarillo p-4 d-flex align-items-center flex-column flex-md-row mb-4",attrs:{"data-aos":"fade-up"}},[_c('img',{staticClass:"me-3",staticStyle:{"width":"90px"},attrs:{"src":require("@/assets/curso/unidad/img-19.svg"),"alt":"Texto descriptivo de la imagen"}}),_c('div',{staticClass:"my-auto"},[_c('p',{staticClass:"pe-xl-3 mb-0"},[_vm._v("Asimismo, el consumo de sustancias también puede llevar a una generación de jóvenes que crecen en entornos desestabilizados por la adicción y la violencia. Estos niños y adolescentes, son más propensos a experimentar traumas, lo que puede tener efectos duraderos en su desarrollo emocional y psicológico. La falta de oportunidades y el acceso limitado a recursos educativos, pue